<template>
  <input type="text" v-model="store.elementData[element.id].title" class="inputtitle handle">
  <textarea ref="text" class="inputstyle autoformtextarea" v-model="store.elementData[element.id].text" rows="6"></textarea>
</template>

<script>
import {useStore} from '@/stores/DndStore.js'

export default {
  data() { return {
    store: useStore(),
    resize_detector: null,
  }},
  props: {
    element: Object,
  },
  beforeMount() {
    // Prevent errors by ensuring store has an object that refers to this
    const me = this.store.elementData[this.$props.element.id]
    if (me == null) {
      console.log("THIS DOES NOT EXIST CREATING")
      this.store.elementData[this.$props.element.id] = {title: "New title", text: "New text", height: 200}
    }
  },
  mounted() {
    this.handleSize()
    this.resize_detector = new ResizeObserver(() => this.handleSize())
    this.resize_detector.observe(this.$refs.text)
  },
  unmounted() {
    this.resize_detector.disconnect()
  },
  watch: {
    displayedText() {
      this.handleSize()
    }
  },
  methods: {
    handleSize() {
      const el = this.$refs.text
      if (el) {
        el.style.height = '10px'
        el.style.height = el.scrollHeight+'px'
      }
    }
  },
  computed: {
    displayedText() {
      if (this.store.elementData[this.element.id])
        return this.store.elementData[this.element.id].text
      return ''
    }
  }
}
</script>

<style>
.autoformtextarea {
  width: 100%;
  margin: 0px;
  line-height: 90%;
  font-size: 0.9em;
  height: auto;
  resize: none;
  overflow-y: hidden;
}
</style>