<template>
<div class="row">
  <div class="col-4 attrbox">
    <span class="attrboxname handle">{{ attributename||'AttrName' }}</span>
    <dnd-input type=number class="attrnumber" :field="field" placeholder="-"></dnd-input>
    <span class="attrbonus">{{ store.plusAndRound(store.getModValue(store[field])) }}</span>
  </div>
  <div class="col">
    <table class="skilltable">
      <tr>    
        <td><input type="checkbox" v-model="store.proficiencies[field+'_save']"></td>
        <td><span class="skillnumber">{{ store.plusAndRound(getSkill(field, field+'_save')) }}</span></td>
        <td class="skilltableskillnamecell"><span>Saving throw</span></td>
      </tr>
      <tr v-for="skill in skills" :key=skill>
        <td><input type="checkbox" v-model="store.proficiencies[skill.toLowerCase()]"></td>
        <td><span class="skillnumber">{{ store.plusAndRound(getSkill(field, skill.toLowerCase())) }}</span></td>
        <td class="skilltableskillnamecell"><span>{{skill}}</span></td>
      </tr>
    </table>
  </div>
</div>
</template>

<script>
import DndInput from '@/components/DndInput.vue'
import {useStore} from '@/stores/DndStore.js'

export default {
  props: {
    skills: {
      type: Array,
      default: () => ['Skill 1', 'Skill 2'],
    },
    attributename: String,
    field: String,
  },
  data() {return {
    store: useStore(),
  }},
  methods: {
    getSkill(attribute, skill) {
      const from_attr = this.store.getModValue(this.store[attribute])
      const from_skill = this.store.proficiencies[skill] * this.store.proficiency_bonus
      return from_attr + from_skill
    }
  },
  components: {
    DndInput
  },
}
</script>

<style>
.attrbox {
  position: relative;
  margin: 5px;
  border: 1px solid #000f;
  border-radius: 3px;
  min-height: 118px;
}
.attrboxname {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
}
.attrnumber {
  position: relative;
  top: 25px;
  left: 0px;
  width: 70%;
  margin: 0 auto;
  font-size: 2em;
  text-align: center;
  border: none;
  background: #fff4;
}
.attrbonus {
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translate(-50%, 15%);
  width: 40px;
  height: 40px;
  background: #fff;
  border: 1px solid #000;
  border-radius: 20%;
  font-size: 1.5em;
}
.skillnumber {
  text-decoration: underline;
  margin: 0 5px 0 5px;
}
.skilltable {
  font-size: 0.8em;
  border-top: 1px solid black;
  width: 100%;
}
.skilltableskillnamecell {
  width: 130px;
}
/* Chrome, Safari, Edge, Opera */
.attrbox input::-webkit-outer-spin-button,
.attrbox input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.attrbox input[type=number] {
  -moz-appearance: textfield;
}
</style>
