<template>
<div class="sheet">
  <div class="container">
    <div class="row">
      <div class="column col-sm">
        <dnd-input field="name" placeholder="Character name"></dnd-input>
        <dnd-input field="race" placeholder="Race"></dnd-input>
        <dnd-input field="level" placeholder="Level" type="number" min=0 max=20></dnd-input>
      </div>
    </div>
    <!-- Columns -->
    <div class="row">
      <div class="column col dndcolumn">
        <DndDragColumn :number="0"></DndDragColumn>
      </div>
      <div class="column col dndcolumn">
        <DndDragColumn :number="1"></DndDragColumn>
      </div>
      <div class="column col dndcolumn">
        <DndDragColumn :number="2"></DndDragColumn>
      </div>
    </div>
    <DndLoadData></DndLoadData>
  </div>
  <div v-if="store.noSave.dragging">
    <draggable 
      v-model="draggable_eater" 
      group="dndwidgetholder"
      item-key="id"
      class="eater"
      ghost-class="eaterGhost">
      <template #item></template>
      <template #header>
        <i class="eaterIcon bi-trash"></i>
      </template>
    </draggable>
  </div>
  <div style="display: inline;">
    <DndWidgetAdder/>
  </div>
  <br>
  <textarea disabled :value="JSON.stringify(store,'','    ')" style="width:90%;height:70vh"></textarea>
  <router-link to="/" style="position: absolute; left:0; top:0; color: #333;">Back</router-link>
</div>
</template>
<script>
import {useStore} from '@/stores/DndStore.js'
import DndInput from '@/components/DndInput.vue'
import DndDragColumn from '@/components/DndDragColumn.vue'
import DndWidgetAdder from '@/components/DndWidgetAdder.vue'
import DndLoadData from '@/components/DndLoadData.vue'
import draggable from 'vuedraggable'

export default {
  data() {return {
    store: useStore(),
    draggable_eater: [],
  }},
  components: {
    DndInput,
    DndDragColumn,
    DndWidgetAdder,
    draggable,
    DndLoadData,
  },
  watch: {
    draggable_eater() {
      // Stop recursion, stop if already empty
      if (this.draggable_eater.length <= 0)
        return
      this.draggable_eater = []
      console.log("EATEN")
      this.store.cleanElementData()
    }
  },
}
</script>
<style>
.column {
  background: rgb(230, 230, 235);
  margin: 1px;
}
.sheet {
  padding-top: 40px;
  background: rgb(172, 136, 113);
}
.box {
  width: 30%;
  min-height: 20px;
  margin: 2px;
  display: inline-block;
  border: 1px solid white;
}
.smallertableheader {
  font-weight: bold;
  font-size: 0.8em;
  color: #0009;
  text-align: left;
}
.inputstyle {
  background: #fff5;
  border-radius: 3px;
  border: none;
  max-width: 100%;
}
#attacks-table input {
  display: inline-block;
  width: 100%;
}
#spellslots-table th {
  width: 12%;
}
#spellslots-table td {
  height: 16px;
  line-height: 0px;
}
.inputtitle {
  background: none;
  border: none;
  text-align: center;
  width: 100%;
  font-variant: small-caps;
}
.eater {
  position: fixed;
  width: 100px;
  height: 100px;
  left: 20px;
  bottom: 20px;
  margin: 0 auto;
  background: #000;
  border-radius: 25px;
  border: 3px solid white;
}
.eaterGhost {
  display: none;
}
.eaterIcon {
  position: relative;
  top: -8px;
  font-size: 80px;
  color: white;
  margin: auto;
}
</style>