<template>
<table id="attacks-table" class="smallertableheader">
  <tr class="handle">
    <th>Name</th>
    <th>Bonus</th>
    <th>Damage/Type</th>
  </tr>
  <tr v-for="atk in store.elementData[element.id].lines" :key="atk">
    <td><input class="inputstyle" type="text" v-model="atk.name"></td>
    <td><input class="inputstyle" type="text" v-model="atk.bonus"></td>
    <td><input class="inputstyle" type="text" v-model="atk.damage"></td>
  </tr>
</table>
<div class="atkbuttongroup">
  <button class="atkbtn btn btn-sm" @click="handleAdd"><i class="bi-plus"></i></button>
  <button class="atkbtn btn btn-sm" @click="handleRemove"><i class="bi-dash"></i></button>
</div>
</template>

<script>
import {useStore} from '@/stores/DndStore.js'

export default {
  props: {
    element: Object,
  },
  data() {return {
    store: useStore(),
  }},
  beforeMount() {
    // Prevent errors by ensuring store has an object that refers to this
    const me = this.store.elementData[this.$props.element.id]
    if (me == null) {
      console.log("THIS DOES NOT EXIST CREATING")
      this.store.elementData[this.$props.element.id] = {
        lines: [
          {name: "Name", bonus: "", damage: ""},
        ]
      }
    }
  },
  methods: {
    handleAdd() {
      this.store.elementData[this.$props.element.id].lines.push({name:'',bonus:'',damage:''})
    },
    handleRemove() {
      if (this.store.elementData[this.$props.element.id].lines.length <= 1)
        return
      this.store.elementData[this.$props.element.id].lines.pop()
    }
  }
}
</script>

<style>
.atkbtn {
  border: 1px solid #2c3e50;
  margin: 1px 2px;
  height: 20px;
  width: 20px;
  position: relative;
}
.atkbtn i {
  color: #2c3e50;
  font-size: 1.1em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.atkbuttongroup {
  text-align: right;
}
</style>