<template>
  <input type="text" :disabled='field==null' :value=showValue @focus="onFocus" @blur="onFocusExit" @input="onInput" :placeholder="placeholder">
</template>

<script>
//  For safekeeping
//<input type="text" v-model=store[field] :placeholder="placeholder">
import { useStore } from '@/stores/DndStore.js'
import { create, all } from 'mathjs'

const math = create(all)

export default {
  props: {
    'field': String,
    'placeholder': {
      type: String,
      default: 'No value'
    },
    'formatter': Function,
  },
  data() {return {
    store: useStore(),
    focused: false,
  }},
  computed: {
    showValue() {
      if (this.formatter)
        return this.formatter(this.getValue)
      return this.getValue
    },
    getValue() {
      if (!this.field)
        return '⚠️ No Field'
      if (this.focused)
        return this.store[this.field]
      // Truthy check
      if (!this.store[this.field])
        return this.store[this.field]
      if (!String(this.store[this.field]).startsWith("=")) 
        return this.store[this.field]
      
      // Eval and show
      const expression = this.store[this.field].slice(1)
      return this.evaluate(expression)
    }
  },
  methods: {
    onInput(e) {
      this.store[this.field] = e.target.value
    },
    onFocus() {
      this.focused = true
    },
    onFocusExit() {
      this.focused = false
    },
    evaluate(expression) {
      try
        {return math.evaluate(expression, this.store)}
      catch
        {return 'Bad input'}
    }
  },
}
</script>

