<template>
  <input type="text" v-model="store.elementData[element.id].title" class="inputtitle handle">
  <textarea ref="text" @mouseup="handPossibleResize" class="inputstyle freeformtextarea" v-model="store.elementData[element.id].text" rows="6" :style="{'height': (store.elementData[element.id].height||30)+'px'}"></textarea>
</template>

<script>
import {useStore} from '@/stores/DndStore.js'

export default {
  data() { return {
    store: useStore(),
  }},
  props: {
    element: Object,
  },
  beforeMount() {
    // Prevent errors by ensuring store has an object that refers to this
    const me = this.store.elementData[this.$props.element.id]
    if (me == null) {
      console.log("THIS DOES NOT EXIST CREATING")
      this.store.elementData[this.$props.element.id] = {title: "New title", text: "New text", height: 200}
    }
  },
  methods: {
    handPossibleResize() {
      const height = Number(this.$refs.text.style.height.replace('px',''))
      this.store.elementData[this.$props.element.id].height = height
    }
  }
}
</script>

<style>
.freeformtextarea {
  width: 100%;
  margin: 0px;
  line-height: 90%;
  font-size: 0.9em;
}
</style>