<template>
<span>Spell list</span>
<br>
<table class="spelltable">
  <tr>
    <th class="hoverinfo handle" title="Prepared?"><h5>P</h5></th>
    <th class="hoverinfo handle" title="Ritual?"><h5>R</h5></th>
    <th>
      <h5 class="handle" style="display:inline;">Name</h5>
      <i class="bi-plus spelllistbutton" @click="handleAdd"></i>
    </th>
  </tr>
  <tr v-for="(line, index) in store.elementData[element.id].lines" :key="index">
    <td><input type="checkbox" v-model="store.elementData[element.id].lines[index].p"></td>
    <td><input type="checkbox" v-model="store.elementData[element.id].lines[index].r"></td>
    <td><input type="text" placeholder="Spell name" class="spellname inputstyle"  v-model="store.elementData[element.id].lines[index].name"></td>
    <td><i class="bi-trash spelllistbutton" @click="handleRemove(index)"></i><i class="bi-box-arrow-up-right"></i></td>
  </tr>
</table>
</template>

<script>
import {useStore} from '@/stores/DndStore.js'

export default {
  props: {
    element: Object,
  },
  data() {return {
    store: useStore(),
  }},
  beforeMount() {
    // Prevent errors by ensuring store has an object that refers to this
    const me = this.store.elementData[this.$props.element.id]
    if (me == null) {
      console.log("THIS DOES NOT EXIST CREATING")
      this.store.elementData[this.$props.element.id] = {
        lines: [
          {p: false, r: false, name: 'Spell name'},
          {p: false, r: true, name: 'Spell 2 name'},
        ]
      }
    }
  },
  methods: {
    handleAdd() {
      this.store.elementData[this.$props.element.id].lines.push(
        {p: false, r: false, name: "New Spell"}
      )
    },
    handleRemove(index) {
      this.store.elementData[this.$props.element.id].lines.splice(index, 1)
    }
  }
}
</script>

<style>
.spelltable {
  width: 100%;
}
.spellname {
  width: 100%;
}
.hoverinfo {
  cursor: help;
}
.spelllistbutton {
  cursor: pointer;
}
</style>