<template>
<div class="portraitHolder" @mouseenter="handleHoverOn" @mouseleave="handleHoverOff">
  <carousel ref="val" :items-to-show="1" :wrapAround="true" :mouseDrag="false">
    <slide v-for="(url, index) in store.elementData[element.id].urls" :key="index">
      <img draggable="false" class="portrait" :src="url" :alt="'picture '+(index+1)">
      <input v-show="focused" v-model="store.elementData[element.id].urls[index]" class="portraitInput" type="text" placeholder="url">
    </slide>
    <template #addons>
      <navigation v-if="focused" />
    </template>
  </carousel>
  <i class="portraitButton handle portraitHandle bi-arrows-move" v-show="focused"></i>
  <i class="portraitButton portraitAdd bi-plus" @click="handleAdd" v-show="focused"></i>
  <i class="portraitButton portraitRemove bi-trash" @click="handleRemove" v-show="focused"></i>
</div>
</template>

<script>
import {useStore} from '@/stores/DndStore.js'
import { Carousel, Slide, Navigation } from 'vue3-carousel';
import 'vue3-carousel/dist/carousel.css';
import { ref } from 'vue';

export default {
  props: {
    element: Object,
  },
  components: {
    Carousel,
    Slide,
    Navigation,
  },
  setup() {
    return {
      val: ref(null)
    }
  },
  data() {return {
    store: useStore(),
    focused: false,
  }},
  beforeMount() {
    // Prevent errors by ensuring store has an object that refers to this
    const me = this.store.elementData[this.$props.element.id]
    if (me == null) {
      console.log("THIS DOES NOT EXIST CREATING")
      this.store.elementData[this.$props.element.id] = {
        urls: ['https://i.pinimg.com/736x/35/dd/f0/35ddf011fbe5b475dacb90ea6ed9dc2a--fantasy-artwork-comic-art.jpg','https://i.imgur.com/jf3yCoM.png'],
      }
    }
  },
  computed: {
    position() {
      if (!this.val)
        return 0
      return this.val.data.currentSlide
    }
  },
  methods: {
    handleHoverOn() {
      this.focused = true
    },
    handleHoverOff() {
      this.focused = false
    },
    handleAdd() {
      this.store.elementData[this.$props.element.id].urls.push('')
    },
    handleRemove() {
      if (this.val.data.maxSlide.value <= 0) {
        return
      }
      this.store.elementData[this.$props.element.id].urls.splice(this.position.value, 1)
      this.val.restartCarousel()
    },
    test(e) {
      e.preventDefault()
    }
  }
}
</script>

<style>
.portrait {
  width: 100%;
  min-height: 100px;
  text-align: center;
  line-height: 100px;
}
.portraitInput {
  position: absolute;
  width: calc(100% - 20px);
  top: 10px;
  left: 50%;
  transform: translate(-50%);
}
.portraitHolder {
  position: relative;
}
.portraitButton {
  color: white;
  background: black;
  padding: 5px;
  line-height: 0px;
  height: 30px;
  width: 30px;
  border-radius: 8px;
  border: 1px solid white;
  position: absolute;
}
.portraitHandle {
  top: 40px;
  left: 10px;
}
.portraitAdd {
  top: 40px;
  left: 40px;
  cursor: pointer;
  color: #afa;
}
.portraitRemove {
  top: 40px;
  left: 70px;
  cursor: pointer;
  color: #faa;
}
</style>