<template>
<draggable class="draggableCol" v-model="store.columns[number]" handle=".handle" group="dndwidgetholder" item-key="id" @start="store.noSave.dragging=true" @end="store.noSave.dragging=false">
  <template #item="{element}">
    <div>
      <!-- Text element -->
      <div v-if="element.type == 'text'">
        <DndTextElement :element=element></DndTextElement>
      </div>
      <!-- Autosize Text element -->
      <div v-if="element.type == 'autotext'">
        <DndAutosizeTextElement :element=element></DndAutosizeTextElement>
      </div>
      <!-- Portrait element -->
      <div v-else-if="element.type == 'portrait'">
        <DndPortraitElement :element=element></DndPortraitElement>
      </div>
      <!-- Spell list element -->
      <div v-else-if="element.type == 'spelllist'">
        <DndSpellListElement :element=element></DndSpellListElement>
      </div>
      <!-- Money element -->
      <div v-else-if="element.type == 'money'">
        <DndMoneyElement :element=element></DndMoneyElement>
      </div>
      <!-- Status element -->
      <div v-else-if="element.type == 'status1'" id="status_cluster">
        <div class="box handle">
          <span>Proficiency</span>
          <br>
          <span>{{ store.proficiency_bonus||0 }}</span>
        </div>
        <div class="box handle">
          <span>P.perception</span>
          <br>
          <span>{{ 10 + store.getModValue(store.wisdom) + store.proficiency_bonus * store.proficiencies.perception }}</span>
        </div>
        <div class="box handle">
          <span>Inspiration</span>
          <br>
          <span>-</span>
        </div>
      </div>
      <!-- Status element -->
      <div v-else-if="element.type == 'status2'" id="status_cluster">
        <div class="box handle">
          <span>AC</span>
          <br>
          <DndInput field="ac" class="inputstyle"></DndInput>
        </div>
        <div class="box handle">
          <span>Initiative</span>
          <br>
          <DndInput field="initiative" class="inputstyle"></DndInput>
        </div>
        <div class="box handle">
          <span>Speed</span>
          <br>
          <DndInput field="speed" class="inputstyle"></DndInput>
        </div>
      </div>
      <!-- Attack -->
      <div v-else-if="element.type == 'attack'" id="attacks_cluster">
        <DndAttackElement :element=element></DndAttackElement>
      </div>
      <!-- Health -->
      <div v-else-if="element.type == 'hp'" id="health_cluster">
        <div class="box">
          <span>Hp</span>
          <br>
          <input class="inputstyle" type="number" v-model="store.hp" :max=store.maxhp>
        </div>
        <div class="box handle">
          <span>Max hp</span>
          <br>
          <DndInput field="maxhp" class="inputstyle"></DndInput>
        </div>
      </div>
      <!-- Attributes -->
      <div v-else-if="element.type == 'attributes'" id="attribute_cluster">
        <dnd-attribute-element field="strength" attributename="Strength" :skills='["Athletics", ]'/>
        <dnd-attribute-element field="dexterity" attributename="Dexterity" :skills='["Acrobatics", "Sleight of Hand", "Stealth", ]'/>
        <dnd-attribute-element field="constitution" attributename="Constitution" :skills='[ ]'/>
        <dnd-attribute-element field="intelligence" attributename="Intelligence" :skills='["Arcana", "History", "Investigation", "Nature", "Religion" ]'/>
        <dnd-attribute-element field="wisdom" attributename="Wisdom" :skills='["Animal Handling", "Insight", "Medicine", "Perception", "Survival" ]'/>
        <dnd-attribute-element field="charisma" attributename="Charisma" :skills='["Deception", "Intimidation", "Performance", "Persuasion" ]'/>
      </div>
      <!-- Single Attribute -->
      <div v-else-if="element.type == 'str'" id="attribute_cluster">
        <dnd-attribute-element field="strength" attributename="Strength" :skills='["Athletics", ]'/>
      </div>
      <div v-else-if="element.type == 'dex'" id="attribute_cluster">
        <dnd-attribute-element field="dexterity" attributename="Dexterity" :skills='["Acrobatics", "Sleight of Hand", "Stealth", ]'/>
      </div>
      <div v-else-if="element.type == 'con'" id="attribute_cluster">
        <dnd-attribute-element field="constitution" attributename="Constitution" :skills='[ ]'/>
      </div>
      <div v-else-if="element.type == 'int'" id="attribute_cluster">
        <dnd-attribute-element field="intelligence" attributename="Intelligence" :skills='["Arcana", "History", "Investigation", "Nature", "Religion" ]'/>
      </div>
      <div v-else-if="element.type == 'wis'" id="attribute_cluster">
        <dnd-attribute-element field="wisdom" attributename="Wisdom" :skills='["Animal Handling", "Insight", "Medicine", "Perception", "Survival" ]'/>
      </div>
      <div v-else-if="element.type == 'cha'" id="attribute_cluster">
        <dnd-attribute-element field="charisma" attributename="Charisma" :skills='["Deception", "Intimidation", "Performance", "Persuasion" ]'/>
      </div>
      <!-- Spellslots -->
      <div v-else-if="element.type == 'spellslots'" id="spellslot_cluster">
        <DndSpellSlotsElement :element=element></DndSpellSlotsElement>
      </div>
      <!-- Fallback element -->
      <div v-else>
        <span class="handle">
          {{ element.type }}
        </span>
      </div>
    </div>
  </template>
</draggable>
</template>

<script>
import draggable from 'vuedraggable'
import {useStore} from '@/stores/DndStore.js'
import DndAttributeElement from '@/components/DndAttributeElement.vue'
import DndTextElement from '@/components/DndTextElement.vue'
import DndAutosizeTextElement from '@/components/DndAutosizeTextElement.vue'
import DndSpellSlotsElement from '@/components/DndSpellSlotsElement.vue'
import DndAttackElement from '@/components/DndAttackElement.vue'
import DndPortraitElement from '@/components/DndPortraitElement.vue'
import DndSpellListElement from '@/components/DndSpellListElement.vue'
import DndMoneyElement from '@/components/DndMoneyElement.vue'
import DndInput from '@/components/DndInput.vue'

export default {
  data() { return {
    store: useStore(),
  }},
  props: {
    number: Number,
  },
  components: {
    draggable,
    DndAttributeElement,
    DndTextElement,
    DndAutosizeTextElement,
    DndSpellSlotsElement,
    DndAttackElement,
    DndPortraitElement,
    DndSpellListElement,
    DndInput,
    DndMoneyElement,
  }
}
</script>

<style>
.draggableCol {
  height: 100%;
}
.handle:hover {
  cursor: move;
}
</style>