<template>
  <div class="row">
    <div class="column col-sm loadcol">
      <div v-for="(char, key) in store.noSave.characters" :key="key" class="loaderbox">
        <h6>{{ char.name }} {{ key }}</h6>
        <button @click=store.load(key)>Load {{key}}</button>
        <br>
        <button @click=overwrite(key)>Overwrite {{key}}</button>
        <br>
        <button @click=export_f(key)>Export</button>
        <br>
        <button @click=store.delete(key)>Delete</button>
      </div>
      <div class="loaderbox">
        <button @click=store.saveAsNew>Save as new to {{store.as_new_index}}</button>
        <br>
        <button @click=import_f>Import as new</button>
      </div>
    </div>
  </div>
</template>

<script>
import {useStore} from '@/stores/DndStore.js'

export default {
  data() { return {
    store: useStore(),
  }},
  methods: {
    export_f(slot) {
      const data = this.store.noSave.characters[slot]
      const strData = JSON.stringify(data)
      navigator.clipboard.writeText(strData).then(() => {
        alert("Worked")
      })
    },
    import_f() {
      navigator.clipboard.readText().then((data) => {
        alert(data)
        this.store.noSave.characters[this.store.as_new_index] = JSON.parse(data)
        localStorage.setItem("characters", JSON.stringify(this.store.noSave.characters))
      })
    },
    overwrite(slot) {
      if (!confirm(`Overwrite slot ${slot}?`))
        return
      this.store.save(slot)
    }
  }
}
</script>

<style>
.loaderbox {
  position: relative;
  height: 160px;
  width: 200px;
  margin: 2px;
  border: 1px dotted #0005;
  border-radius: 5px;
  float: left;
}
</style>