<template>
<table>
  <tr>
    <td><input class="moneyfield inputstyle" type="text" placeholder="pp" v-model="store.elementData[element.id].pp"></td>
    <td><input class="moneyfield inputstyle" type="text" placeholder="gp" v-model="store.elementData[element.id].gp"></td>
    <td><input class="moneyfield inputstyle" type="text" placeholder="sp" v-model="store.elementData[element.id].sp"></td>
    <td><input class="moneyfield inputstyle" type="text" placeholder="cp" v-model="store.elementData[element.id].cp"></td>
  </tr>
  <tr>
    <th class="handle"><span>pp</span></th>
    <th class="handle"><span>gp</span></th>
    <th class="handle"><span>sp</span></th>
    <th class="handle"><span>cp</span></th>
  </tr>
</table>
</template>

<script>
import {useStore} from '@/stores/DndStore.js'

export default {
  props: {
    element: Object,
  },
  data() {return {
    store: useStore(),
  }},
  beforeMount() {
    // Prevent errors by ensuring store has an object that refers to this
    const me = this.store.elementData[this.$props.element.id]
    if (me == null) {
      console.log("THIS DOES NOT EXIST CREATING")
      this.store.elementData[this.$props.element.id] = {
        pp: 0,
        gp: 0,
        sp: 0,
        cp: 0,
      }
    }
  },
}
</script>

<style>
.moneyfield {
  width: 65%;
  text-align: center;
}
</style>