<template>
  <h2>Add from here {{ val }}</h2>
  <select v-model="val" @change="handleSelect" name="widget_adder" id="widget_adder">
    <option value="">--Add widget--</option>
    <option value="autotext">Text</option>
    <option value="text">Scrollable Text</option>
    <option value="spellslots">Spellslots</option>
    <option value="portrait">Portrait</option>
    <option value="spelllist">Spell List</option>
    <option value="useless">Useless</option>
    <option value="money">Money</option>
  </select>
</template>

<script>
import {useStore} from '@/stores/DndStore.js'

export default {
  data() { return {
    store: useStore(),
    val: ""
  }},
  methods: {
    handleSelect() {
      this.addElement()
      // Unselect afterwards
      this.val = ""
    },
    addElement() {
      this.store.addElement(2, this.val)
    }
  }
}
</script>

